<template>
  <div id="income">
    <div class="tit">分润收益统计</div>
    <div class="income_chars">
      <div class="incomes today"><span class="i_tips">今日统计</span>
        <div class="income_nums">
          <div class="income_item"><img src="/static/icon/12.png" width="31">
            <div>
              <p>{{list.Today.total_success_count}}</p>
              <span>成功订单</span>
            </div>
          </div>
          <div class="income_item" style="border-right: 0px none;"><img src="/static/icon/8.png" width="31" height="31">
            <div>
              <p>{{list.Today.total_success_money}}元</p>
              <span>分润收益</span>
            </div>
          </div>
        </div>
      </div>
      <div class="incomes yestoday"><span class="i_tips">昨日统计</span>
        <div class="income_nums">
          <div class="income_item"><img src="/static/icon/12.png" width="31">
            <div>
              <p>{{list.Yestoday.total_success_count}}</p> <span>成功订单</span>
            </div>
          </div>
          <div class="income_item" style="border-right: 0px none;"><img src="/static/icon/8.png" width="31" height="31">
            <div>
              <!---->
              <p>{{list.Yestoday.total_success_money}}元</p> <span>分润收益</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="income_list">
      <DatePicker type="daterange" split-panels :value="time" placeholder="指定订单时间" style="width: 240px" @on-change="datechange($event)"></DatePicker>
      <div class="store_bottom">
        <table>
        <thead>
        <tr>
            <th>支付订单数</th>
            <th>总分润收益</th>
        </tr>
        </thead>
        <tbody>
          <tr>
            <td>{{list.Total.total_success_count}}</td>
            <td>{{list.Total.total_success_money}} 元</td>
          </tr>
        </tbody>
      </table>
    </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
          spin:true,
          time:"",
          uid:"",
          list:{
            Total:{
              total_success_count:0,
              total_success_money:0
            },
            Yestoday:{
              total_success_count:0,
              total_success_money:0
            },
            Today:{
              total_success_count:0,
              total_success_money:0
            }
          },
      }
    },
    mounted() {
      if(typeof(this.$route.params.uid) != undefined){
        this.uid = this.$route.params.uid;
      }
      this.time = [this.getDay(0),this.getDay(1)];
      this.getprofit(this.time,this.uid);
    },
    methods: {
      datechange(n) {
        this.time = n;
        this.getprofit(this.time,this.uid);
      },
      getprofit(time,uid){
        var profitname = this.$md5(JSON.stringify(time,uid));
        if(localStorage.getItem(profitname)){
          var profit = JSON.parse(localStorage.getItem(profitname));
          if(profit.time < new Date().getTime()){
            this.ajaxlist(time,uid);
          }else{
            this.list = profit.data;
          }
        }else{
          this.ajaxlist(time,uid);
        }
      },
      getDay(num) {
          var today = new Date();
          var nowTime = today.getTime();
          var ms = 24*3600*1000*num;
          today.setTime(parseInt(nowTime + ms));
          var oYear = today.getFullYear();
          var oMoth = (today.getMonth() + 1).toString();
          if (oMoth.length <= 1) oMoth = '0' + oMoth;
          var oDay = today.getDate().toString();
          if (oDay.length <= 1) oDay = '0' + oDay;
          return oYear+'-'+oMoth+'-'+oDay;
      },
      ajaxlist(time,uid){
        this.spin = true;
        this.$http.post('api/user/profit_statistics',{code:100,time:time,uid:uid}, res => {
              this.spin = false;
             if (res.status == 200) {
                 if(res.data.code == 200){
                   var profitname = this.$md5(JSON.stringify(time,uid));
                   var local = {};
                       local.time = new Date().getTime() + 20000;
                       local.data = res.data.data;
                   localStorage.setItem(profitname,JSON.stringify(local));
                    this.list = res.data.data;
                 }else if(res.data.code == 201){
                   this.$Message.warning(res.data.msg);
                 }
             } else {
                console.log(res.message);
             }
        });
      }
    }
  }
</script>

<style>
</style>



